import React from 'react'
import {graphql, useStaticQuery,Link} from 'gatsby'

const PropertyCitiesHome4 = () => {
	const data = useStaticQuery(graphql`
	query{
		pc1: file(relativePath: {eq: "property/pc12.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc2: file(relativePath: {eq: "property/pc13.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc3: file(relativePath: {eq: "property/pc14.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc4: file(relativePath: {eq: "property/pc15.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        pc5: file(relativePath: {eq: "property/pc16.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc6: file(relativePath: {eq: "property/pc17.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
	}`
)
	return (
		<section id="property-city" className="property-city pb30">
		<div className="container">
			<div className="row">
				  	<div className="col-lg-6 offset-lg-3">
					<div className="main-title text-center">
						<h2>Find Properties in These Cities</h2>
						<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. <a className="float-right" href="#">View All <span className="flaticon-next"></span></a></p>
					</div>
				    </div>
				</div>
				<div className="row">
	  				<div className="col-sm-6 col-lg-4 col-xl-4">
					  <Link to="/half-map/v4">
						<div className="properti_city style2">
						  	<div className="thumb"><img className="img-fluid w100" src={data.pc1.childImageSharp.fluid.originalImg} alt="pc1.jpg" /></div>
								<div className="details">
							  		<h4>Miami</h4>
							  		<p>24 Properties</p>
								</div>
						</div>
					  </Link>
	  				</div>
	  				<div className="col-sm-6 col-lg-4 col-xl-4">
					  <Link to="/half-map/v4">
						<div className="properti_city style2">
						  	<div className="thumb"><img className="img-fluid w100" src={data.pc2.childImageSharp.fluid.originalImg} alt="pc2.jpg" /></div>
								<div className="details">
								  	<h4>Los Angeles</h4>
								  	<p>18 Properties</p>
								</div>
						</div>
					  </Link>
	  				</div>
	  				<div className="col-sm-6 col-lg-4 col-xl-4">
					  <Link to="/half-map/v4">
						<div className="properti_city style2">
						  	<div className="thumb"><img className="img-fluid w100" src={data.pc3.childImageSharp.fluid.originalImg} alt="pc3.jpg" /></div>
								<div className="details">
							  		<h4>New York</h4>
							  		<p>89 Properties</p>
								</div>
						</div>
					  </Link>
	  				</div>
	  				<div className="col-sm-6 col-lg-4 col-xl-4">
					  <Link to="/half-map/v4">
						<div className="properti_city style2">
						  	<div className="thumb"><img className="img-fluid w100" src={data.pc4.childImageSharp.fluid.originalImg} alt="pc4.jpg" /></div>
								<div className="details">
								  	<h4>Florida</h4>
								  	<p>47 Properties</p>
								</div>
						</div>
					  </Link>
	  				</div>
                    <div className="col-sm-6 col-lg-4 col-xl-4">
					  <Link to="/half-map/v4">
						<div className="properti_city style2">
							<div className="thumb"><img className="img-fluid w100" src={data.pc5.childImageSharp.fluid.originalImg} alt="pc11.jpg"/></div>
									<div className="details">
										<h4>Orlando</h4>
										<p>89 Properties</p>
									</div>
						</div>
					  </Link>
					</div>
					<div className="col-sm-6 col-lg-4 col-xl-4">
					  <Link to="/half-map/v4">
						<div className="properti_city style2">
							<div className="thumb"><img className="img-fluid w100" src={data.pc6.childImageSharp.fluid.originalImg} alt="pc11.jpg"/></div>
									<div className="details">
										<h4>Atlanta</h4>
										<p>89 Properties</p>
									</div>
						</div>
					  </Link>
					</div>
				</div>
  			</div>
		</section>
	)
}

export default PropertyCitiesHome4